'use client';

import { PhotoGallery } from './PhotoGallery';
import Link from 'next/link';

interface Column {
  id: string;
  section_id: string;
  column_number: 1 | 2;
  content_type: 'rich_text' | 'photo_gallery' | 'references';
  content_data: any;
  created_at: string;
  updated_at: string;
}

interface Section {
  id: string;
  title?: string;
  page_type: 'activity' | 'event' | 'accommodation' | 'room_type' | 'custom' | 'home';
  page_id: string;
  display_order: number;
  created_at: string;
  updated_at: string;
  columns: Column[];
}

interface SectionRendererProps {
  section: Section;
  className?: string;
}

/**
 * SectionRenderer Component
 * 
 * Renders a section with its columns on guest-facing pages.
 * Supports:
 * - Rich text content
 * - Photo galleries with display modes (gallery/carousel/loop)
 * - References to other entities
 * - Single or two-column layouts
 */
export function SectionRenderer({ section, className = '' }: SectionRendererProps) {
  const isTwoColumn = section.columns.length === 2;

  return (
    <div className={`section ${className}`} data-testid={`section-${section.id}`}>
      {/* Optional Section Title */}
      {section.title && (
        <h2 className="text-2xl font-bold text-jungle-800 mb-4">{section.title}</h2>
      )}

      {/* Column Grid */}
      <div
        className={`grid gap-6 ${
          isTwoColumn ? 'grid-cols-1 md:grid-cols-2' : 'grid-cols-1'
        }`}
      >
        {section.columns.map((column) => (
          <div key={column.id} data-testid={`column-${column.id}`} className="column">
            {/* Rich Text Content */}
            {column.content_type === 'rich_text' && (
              <div
                className="prose prose-lg max-w-none text-gray-700"
                dangerouslySetInnerHTML={{ __html: column.content_data?.html || '' }}
              />
            )}

            {/* Photo Gallery */}
            {column.content_type === 'photo_gallery' && (
              <PhotoGallery
                photoIds={column.content_data?.photo_ids || []}
                displayMode={column.content_data?.display_mode || 'gallery'}
              />
            )}

            {/* References */}
            {column.content_type === 'references' && (
              <div className="space-y-3" data-testid="references">
                {(column.content_data?.references || []).map((ref: any, index: number) => (
                  <div
                    key={index}
                    data-testid={`reference-${index}`}
                    className="border border-sage-200 rounded-lg p-4 hover:border-jungle-400 transition-colors"
                  >
                    <div className="flex items-start justify-between">
                      <div>
                        <span className="inline-block px-2 py-1 text-xs font-medium bg-sage-100 text-sage-700 rounded mb-2 reference-type">
                          {ref.type}
                        </span>
                        <h3 className="text-lg font-semibold text-jungle-800 reference-name">
                          {ref.name}
                        </h3>
                        {ref.description && (
                          <p className="text-sm text-gray-600 mt-1">{ref.description}</p>
                        )}
                      </div>
                      {ref.id && (
                        <Link
                          href={`/${ref.type}/${ref.id}`}
                          className="text-jungle-600 hover:text-jungle-700 text-sm font-medium"
                        >
                          View â†’
                        </Link>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}
